<template>
  <div class="registerBox">
    <header>
      <div class="logobox">
        <div class="logo"></div>
        <div class="logontag">Orange</div>
      </div>
      <router-link class="logintxt" to="/login">
        <span class="cancel">跳过</span>
      </router-link>
      
    </header>

    <div class="inputBox">
      <div class="notice">为了您的信息安全请绑定手机号</div>

      <!-- 注册页面 -->
      <div class="registermodule">
        <van-cell-group :border="false">
          <van-field type="text" v-model="phone.value"
          placeholder="请输入手机号码" :error-message="phone.errorMsg"
          @input="validphone(phone.value)"
          :border="false"/>

          <van-field center clearable placeholder="请输入短信验证码" 
          :border="false" v-model="vefcode.value" @input="validcode(vefcode.value)">
            <van-button type="info" slot="button" size="small" @click="verificationCode(vefcode)">验证码</van-button>
          </van-field>

          <van-button class="loginBtn" :loading="isLoading" 
              type="info" block loading-text="登录中..." 
              text="登录"  @click="login"  
          />      
          <div class="login">
            <router-link class="logintxt" to="/login">已有账号？登录</router-link>
          </div>
        </van-cell-group>
      </div>
    </div>

    <footer>
      <span>注册代表您同意该软件
        <a href="">用户服务协议</a>和
        <a href="">隐私政策</a>
      </span>
    </footer>

  </div>
</template>

<script>
import { Dialog } from 'vant';

import {validForm} from '../tool/validForm'

import {createNamespacedHelpers } from "vuex";
const {mapState,mapMutations} = createNamespacedHelpers('phoneRegisterModule');

export default {
  name: "phoneRegister",
  
  // 计算属性
  computed:{
    // 数组的导入方式
    ...mapState(['phone','vefcode','isValid','isLoading'])
  },

  methods:{
    // 登录验证  
    login(v){
        // console.log('登录');
        let self = this;

        self.$store.commit('phoneRegister/login',true); //登录

        // 5S内随机时间
        let randomTime = Math.random() * 3 * 1000;
        
        let isValid = validForm.validphone(v);
        //登录延时
        setTimeout(() =>{

          // 4获取所有用户信息(一开没有登录始拿不到)
          let allusersInfo = localStorage.getItem('allusersInfo');
          allusersInfo = allusersInfo ? JSON.parse(allusersInfo) : []; // 判断有没有值否则会报错一个null
          // console.log('allusersInfo=>',allusersInfo);

          self.$router.push({name:'login'});
        },randomTime)

    },

    // 手机号码验证
    validphone(v){
      // console.log('value=>',v); //是否有值传入
      
      let isValid = validForm.validphone(v);
      this.$store.commit('phoneRegisterModule/validphone',isValid);
      // this.$store.commit('phoneRegisterModule/passFun');
    },

    verificationCode(v){
      
      this.$store.commit('phoneRegisterModule/verificationCode');
      let value = v.arr.join("");
      // console.log('value=>',value); //是否有值传入

      Dialog.alert({
        title: '验证码为：(记住了再确定哦！)',
          message:value
        }).then(() => {
          //  on close
      });
    
    },

    validcode(v){
      // console.log('v.value=>',v);
      this.$store.commit('phoneRegisterModule/validcode');
    },

  }
};
</script>

<style lang="less" scoped>
  .registerBox{
    height: 100%;
    
    header{
      padding: 30px 15px;
      .logobox{
        width: 60px;
        height: 60px;
        background-color: #fff;
        border-radius: 50%;
        .logo{
          width: 60px;
          height: 60px;
          border-radius: 50%;
          background: url("../assets/images/orangeblack.png") no-repeat center center;
          background-size: 50px 50px;
        }
        .logontag{
          font-size: 22px;
          color:#000; 
          font-family: "华文行楷";
          font-weight: bold;
          text-align: center;
          text-indent: 40px;
          margin-top: -8px;
        }
      }
      .cancel{
        position: absolute;
        right: 20px;
        top: 20px;
        color: #333;
      }
    }

    .inputBox{
      padding: 15px;
      .notice{
        font-size: 18px;
        color:#000; 
        padding: 6px 0;
        text-align: center;
        font-family: "微软雅黑"
      }
      .registermodule{
        padding: 10px;
      }
      .van-cell-group {
        color: #ddd;
        background-color: transparent;
        padding:10px;
        // border:1px solid #ddd;
      }
      
      .van-cell {
        // background-color: transparent;
        border-bottom: 1px solid #aaa;
        margin: 16px 0;
      }
      /deep/.van-button.loginBtn {
        width: 100%;
        border-radius: 10px;
        margin: 40px auto 0px;
        color: #fff;
        background-color:#aaa;
        // background-image: repeating-linear-gradient(
        //   to right,
        //   #fd4154,
        //   #fe7278,
        //   #fe959b
        // );
        font-size: 18px;
        border: none;
        
        &:hover{
         background-color:#F32F6F; 
         color: #fff;
        }
      }

      .van-button--disabled {
        opacity: 0.9; 
      }
 
      .login{
        margin-top: 10px;
        text-align: center;
        .logintxt{
          font-size: 14px;
          color: #333;
        }
      }
    }

    footer{
      width: 100%;
      position: fixed;
      bottom: 20px;
      text-align: center;
    }

    /deep/.van-button--info {
      color: #fff;
      background-color:rgb(250, 93, 145);
      border: 0.02667rem solid #F32F6F;
    }
  }


</style>
